  button[disabled] {
    color: #999999;
    background-color: #333333;
    cursor: not-allowed; }

input[type="text"] {
  width: 100%;
  padding: 10px;
  padding-top: 5px;
  border: 0;
  border-radius: 0;
  border-bottom: 5px solid #999999;
