  background-color: #444444;
  font-size: 15px;
  outline: 0;
  -webkit-appearance: none; }
  input[type="text"]:focus {
    border-bottom-color: #efefef; }
  input[type="text"][disabled] {
    color: #999999;
    cursor: not-allowed; }

label {
  display: block;
  padding: 10px;
  padding-bottom: 5px;
  background-color: #444444;
  font-weight: 900; }

