  background-color: #000000;
  font-size: 15px;
  font-family: "Arial", sans-serif;
  line-height: 1.5; }

hr {
  margin-top: 20px;
  margin-bottom: 20px;
  border-style: solid;
  border-color: #444444; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  text-transform: uppercase; }

.form__title {
  margin-top: 10px;
  margin-bottom: 10px; }

.settings {
  display: flex;
  padding: 20px;
  flex-wrap: wrap; }

.settings__header {
  width: 100%;
  margin-bottom: 20px; }

.settings__section {
  width: 50%;
  background-color: #333333; }

.settings__section-header {
  padding: 10px;
  background-color: #444444; }

.settings__section-content {
  padding: 10px; }

.status__field + .status__field {
  margin-top: 10px; }

.status__field-label {
  padding: 10px;
  padding-bottom: 5px;
  font-weight: 900; }

.status__field-value {
  padding: 10px;
  padding-top: 5px;
  border-bottom: 5px solid #444444; }

.tick {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #999999;
  transition: background .2s linear; }

.tick--active {
  background-color: #00ff00; }

.view {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: auto;
  transform: translate3d(0, -100%, 0); }

.view--active {
  transform: translate3d(0, 0, 0); }



